    margin-top: 29px;
    padding-right: 45%;
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .form__lead {
    margin-top: 9px;
    padding-right: 33%;
    grid-column: 1/2;
  }
}

.form__fieldset {
  display: grid;
  margin: 0;
  padding: 0;
  row-gap: 13px;
  border: none;
}
@media (min-width: 768px) {
  .form__fieldset {
    row-gap: 20px;
  }
}
@media (min-width: 1440px) {
  .form__fieldset {
    grid-column: 1/-1;
  }
}

.form__fieldset--cats {
  margin-top: 26px;
}
@media (min-width: 768px) {
  .form__fieldset--cats {
    margin-top: 45px;
    row-gap: 20px;
  }
}
@media (min-width: 1440px) {
  .form__fieldset--cats {
    grid-column: 1/2;
  }
}

.form__fieldset--target {
  margin-top: 46px;
  color: var(--black-3);
}
@media (min-width: 768px) {
  .form__fieldset--target {
    margin-top: 73px;
    padding: 24px 28px;
    border: 1px solid var(--grey-8);
    row-gap: 13px;
  }
}
@media (min-width: 1440px) {
  .form__fieldset--target {
    margin-top: 45px;
    grid-column: 2/3;
  }
}

.form__fieldset--user {
  margin-top: 27px;
}
@media (min-width: 768px) {
  .form__fieldset--user {
    margin-top: 46px;
  }
}
@media (min-width: 1440px) {
  .form__fieldset--user {
    margin-top: 40px;
    column-gap: 6%;
    grid-template-columns: repeat(2, 1fr);
  }
}

.form__fieldset--review {
  margin-top: 7px;
}
@media (min-width: 768px) {
  .form__fieldset--review {
    margin-top: 40px;
  }
}

.form__fieldset--additional {
  margin-top: 3px;
  color: var(--black);
}
@media (min-width: 768px) {
  .form__fieldset--additional {
    margin-top: 36px;
    column-gap: 9.5%;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 30px;
  }
}
@media (min-width: 1440px) {
  .form__fieldset--additional {
    margin-top: 36px;
    column-gap: 6.5%;
    grid-template-columns: repeat(4, 1fr);
  }
}

.form__label {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 5%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__label {
    column-gap: 20px;
  }
}

.form__label--radio:active,
.form__label--checkbox:active {
  opacity: 0.3;
}

.form__text {
  flex-grow: 1;
  width: 100px;
  padding: 10px 15px;
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  color: inherit;
  text-transform: inherit;
  border: 1px solid var(--grey-8);
}
.form__text:hover {
  border-color: var(--grey-3);
}
.form__text:focus {
  outline: 2px solid var(--grey);
  outline-offset: -1px;
}
.form__text:invalid {
  outline-color: var(--orange);
}

.form__input-icon {
  position: absolute;
  width: 20px;
  height: 20px;
}
.form__input-icon--email {
  right: 20px;
  mask: url("../img/interface/other/icon-email.svg") no-repeat center;
  background-color: var(--black-3);
}
.form__input-icon--tel {
  right: 18px;
  mask: url("../img/interface/other/icon-phone.svg") no-repeat center;
  background: var(--black);
}

.form__text:focus + .form__input-icon {
  background-color: var(--green);
}

.form__text:invalid + .form__input-icon {
  background-color: var(--orange);
}

.form__radio {
  display: flex;
  width: 30px;
  height: 30px;
  margin: 0;
  border: 1px solid var(--grey-3);
  border-radius: 50%;
  cursor: pointer;
  -webkit-appearance: none;
}
@media (min-width: 768px) {
  .form__radio {
    width: 40px;
    height: 40px;
  }
}
.form__radio::before {
  content: "";
  display: block;
  width: 14px;
  height: 14px;
  margin: auto;
  background-color: var(--green);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .form__radio::before {
    width: 18px;
    height: 18px;
  }
}
.form__radio:checked::before {
  opacity: 1;
}
.form__radio:hover {
  border-color: var(--grey-9);
}

.form__checkbox {
  display: flex;
  width: 30px;
  height: 30px;
  margin: 0;
  border: 1px solid var(--grey-3);
  cursor: pointer;
  -webkit-appearance: none;
}
@media (min-width: 768px) {
  .form__checkbox {
    width: 40px;
    height: 40px;
  }
}
.form__checkbox::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  margin: auto;
  background-image: url("../img/interface/other/icon-tick.svg");
  background-size: contain;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .form__checkbox::before {
    width: 26px;
    height: 26px;
  }
}
.form__checkbox:checked::before {
  opacity: 1;
}
.form__checkbox:hover {
  border-color: var(--grey-9);
}

.form__textarea {
  width: 100%;
  min-height: 150px;
  padding: 7px 12px;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  color: inherit;
  text-transform: inherit;
  border: 1px solid var(--grey-8);
  resize: unset;
}
@media (min-width: 768px) {
  .form__textarea {
    min-height: 173px;
    padding: 7px 19px;
  }
}

/* stylelint-disable */
.form__text::-webkit-outer-spin-button,
.form__text::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.form__text[type=number] {
  -moz-appearance: textfield;
}

/* stylelint-enable */
.form__footer {
  display: grid;
  margin-top: 34px;
}
@media (min-width: 768px) {
  .form__footer {
    margin-top: 56px;
    column-gap: 10%;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1440px) {
  .form__footer {
    margin-top: 60px;
    column-gap: 6.5%;
    grid-column: 1/-1;
  }
}

.form__reminder {
  margin-top: 21px;
  justify-self: center;
  font-size: 14px;
  line-height: 16px;
  font-family: "Arial", sans-serif;
  text-transform: initial;
}
@media (min-width: 768px) {
  .form__reminder {
    align-self: center;
    margin-top: 0;
    font-size: 16px;
    justify-self: start;
  }
}